<template>
  <div class="tab-wrap">
    <div class="tab">
      <router-link tag="div" class="tab-item" to="/homePage">
        <div class="icon icon-home"></div>
        <span class="tab-link">首页</span>
      </router-link>
      <router-link tag="div" class="tab-item" to="/credit">
        <div class="icon icon-coin-yen"></div>
        <span class="tab-link">信用</span>
      </router-link>
      <router-link tag="div" class="tab-item" to="/me">
        <div class="icon icon-user"></div>
        <span class="tab-link">我</span>
      </router-link>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .tab-wrap
    position: fixed;
    width: 100%;
    bottom: 0;
    .tab
      display: flex
      height: min-height 44px
      padding-bottom 4px
      font-size: $font-size-medium
      border-top: 1px solid #eee
      .tab-item
        flex: 1
        text-align: center
        .tab-link
          font-size $font-size-medium
          color: $color-text-l
        .icon
          padding 4px 0
          font-size 22px
          color: $color-text-l
        &.router-link-active
          .tab-link
            color: $color-theme
          .icon
            color: $color-theme
</style>



